import { Host, Component, Prop, h, State } from '@stencil/core';

@Component({
  tag: 'dda-home-banner',
  styleUrls: ['home-banner.css', '../../global/global.css'],
  shadow: false,
})
export class DdaHomeBanner {
  @Prop() images: { src: string; title: string; subtitle: string; link: string }[] = [];
  @Prop() autoplay: boolean = true;
  @Prop() interval: number = 3000;

  @State() currentSlide: number = 0;

  private slideInterval: any;

  componentDidLoad() {
    if (this.autoplay) {
      this.startAutoplay();
    }
  }

  disconnectedCallback() {
    this.stopAutoplay();
  }

  startAutoplay() {
    this.slideInterval = setInterval(() => {
      this.nextSlide();
    }, this.interval);
  }

  stopAutoplay() {
    clearInterval(this.slideInterval);
  }

  nextSlide() {
    this.currentSlide = (this.currentSlide + 1) % this.images.length;
  }

  prevSlide() {
    this.currentSlide = (this.currentSlide - 1 + this.images.length) % this.images.length;
  }

  setSlide(index: number) {
    this.currentSlide = index;
  }

  render() {
    return (
      <Host>
        <div class="home-slider">
          {this.images.map((image, index) => (
            <div class={{ 'slider-item': true, 'active': index === this.currentSlide }}>
              <img src={image.src} alt={`banner-${index}`} />
              <div class="slider-content">
                <h2>{image.title}</h2>
                <p>{image.subtitle}</p>
                <a href={image.link} class="slider-button">Learn More</a>
              </div>
            </div>
          ))}
          <div class="slider-nav">
            <button class="prev" onClick={() => this.prevSlide()}>&#10094;</button>
            <div class="slider-dots">
                {this.images.map((_, index) => (
                <span 
                    class={{ 'dot': true, 'active-dot': index === this.currentSlide }} 
                    onClick={() => this.setSlide(index)}
                ></span>
                ))}
            </div>
            <button class="next" onClick={() => this.nextSlide()}>&#10095;</button>
          </div>
        </div>
      </Host>
    );
  }
}
